import React from 'react';
import {Link} from 'react-router-dom';
function Header(){
    return(
        <div class="header">
            <div class="inner_header">
                <div class="logo_container">
                <h1>MY<span class="logo_part">APP</span></h1>
            </div>

            <ul class="navigation">
                <li><a><Link to="/">Produkte</Link></a></li>
                <li><a href="#"><Link to="/lösungen">Lösungen</Link></a></li>
                <li><a href="#"><Link to="/tools">Tools</Link></a></li>
                <li><a>Unternehmen</a></li>
            </ul>
        </div>
     </div>
    )
}
export default Header;